.navbar{
    height: 50px;
    border-bottom: 0.5px solid rgba(231, 228, 228);
    display: flex;
    align-items: center;
    font-size: 14px;
    color: #555;
    background-color: #fff;

    .wrapper{
        width: 100%;
        padding: 20px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .search{
            display: flex;
            align-items: center;
            border: 0.5px solid lightgray;
            padding: 3px;
        }

        input{
            border: none;
            outline: none;
            background: transparent;

            &::placeholder{
                font-size: 12px;
            }
        }

        .items {
            display: flex;

            .item{
                display: flex;
                align-items: center;
                margin-right: 20px;
                position: relative;

                .icon{
                    font-size: 20px;
                }
                .Avatar{
                    width: 30px;
                    height: 30px;
                    border-radius: 50%;
                }
                .counter{
                    width: 15px;
                    height: 15px;
                    background-color: red;
                    border-radius: 50%;
                    color: #fff;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    font-size: 10px;
                    font-weight: bold;
                    position: absolute;
                    top: -4px;
                    right: -4px;
                }
            }
        }
    }
}